  .projects-section {
      text-align: center;
      background-color: #e4e4e4;
      padding: 20px;
      height: 700px;
    }
    
    .projects-section h1 {
      font-size: 2.5rem;
      margin-bottom: 10px;
    }
    
    .projects-section p {
      font-size: 1.2rem;
      color: gray;
      margin-bottom: 20px;
    }
    
    .projects-divider {
      border: 0;
      height: 5px;
      background: #007bff;
      width: 50%;
      margin: 0 auto 30px;
    }
    
    .projects-container {
      background: #dedede;
      display: flex;
      flex-direction: column;
      gap: 20px;
      max-width: 800px;
      margin: 0 auto;
    }
    
    .project-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #ededed;
      padding: 25px;
      border-radius: 8px;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    }
    
    .project-info {
      display: flex;
      align-items: center;
    }
    
    .project-icon {
      color: #007bff;
      font-size: 1.5rem;
      margin-right: 10px;
    }
    
    .project-title {
      font-size: 1.2rem;
      font-weight: bold;
      color: orange;
      margin: 0;
    }
    
    .project-tech {
      font-size: 0.9rem;
      color: gray;
    }
    
    .project-link {
      display: inline-block;
      margin-top: 5px;
      padding: 5px 15px;
      background: #18dc56;
      color: #ffffff;
      border-radius: 5px;
      text-decoration: none;
      font-size: 0.9rem;
    }
    
    .project-link:hover {
      background: #00ff55;
    }
    
    .project-year {
      background: #270cda;
      color: #ffffff;
      padding: 10px 15px;
      border-radius: 5px;
      font-size: 1rem;
    }


