.contact-section {
    text-align: center;
    background-color: #f4f4f4;
    padding: 20px;
    margin-top: 180px;
  }
  
  .contact-section h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .contact-section p {
    font-size: 1.2rem;
    color: gray;
    margin-bottom: 20px;
  }
  
  .contact-divider {
    border: 0;
    height: 2px;
    background: #007bff;
    width: 50%;
    margin: 0 auto 30px;
  }
  
  .contact-icons {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .contact-icons img {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  
  .contact-icons img:hover {
    transform: scale(1.1);
    transition: transform 0.3s;
  }
  
  .contact-image img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin: 20px 0;
  }
  
  .contact-footer {
    background: #dcdcdc;
    color: gray;
    padding: 10px 0;
    font-size: 0.9rem;
  }
  
  .contact-footer p {
    margin: 0;
  }
 